<template>
  <div class="hello">
    <ul class="nav">
      <li class="nav-item">
        <router-link class="nav-link" to="/page/child">1</router-link>
      </li>
      <li class="nav-item">
        <router-link class="nav-link" to="/page/child2">2</router-link>
      </li>
      <li class="nav-item">
        <router-link class="nav-link" to="/page/child3">3</router-link>
      </li>
      <li class="nav-item">
        <a href="#" class="nav-link" @click.prevent="updatePath"></a>
      </li>
      <li class="nav-item">
        <a href="#" class="nav-link" @click.prevent="lastPath"></a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
   methods:{
    updatePath(){
      this.$router.push('/page/child2');
    },
    lastPath(){
      this.$router.back();
    }
  }
};
</script>




