<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div
            style="
              height: 150px;
              background-size: cover;
              background-position: center;
            "
            :style="{ backgroundImage: `url(${item.imageUrl})` }"
          ></div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{
              item.category
            }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">: {{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">
                {{ item.origin_price | currency }} 
              </div>
              <del class="h6" v-if="item.price"
                > {{ item.origin_price | currency }} </del
              >
              <div class="h5" v-if="item.price">
                 {{ item.price | currency }} 
              </div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button
              type="button"
              class="btn btn-outline-secondary btn-sm"
              @click="getProduct(item.id)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="status.loadingItem === item.id"
              ></i>
              
            </button>
            <button
              type="button"
              class="btn btn-outline-danger btn-sm ml-auto"
              @click="addtoCart(item.id)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="status.loadingItem === item.id"
              ></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              {{ product.title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="" />
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">
                {{ product.description }}
              </footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">
                {{ product.origin_price }} 
              </div>
              <del class="h6" v-if="product.price"
                > {{ product.origin_price }} </del
              >
              <div class="h4" v-if="product.price">
                 {{ product.price }} 
              </div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                 {{ num }} {{ product.unit }}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
               <strong>{{ product.num * product.price }}</strong> 
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addtoCart(product.id, product.num)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="product.id === status.loadingItem"
              ></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="my-5 row justify-content-center" v-if="cart.length != 0">
      <div class="my-5 row justify-content-center">
        <table class="table">
          <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"
                >
                  <!-- <button type="button" class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"> -->
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon"></div>
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right"></td>
              <td class="text-right">{{ cart.total }}</td>
            </tr>
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success"></td>
              <td class="text-right text-success">{{ cart.final_total }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
          <input
            type="text"
            class="form-control"
            placeholder=""
            v-model="coupon_code"
          />
          <div class="input-group-append">
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="addCoupon"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Form -->
    <div class="my-5 row justify-content-center">
      <ValidationObserver class="col-md-6" v-slot="{ invalid }">
        <form @submit.prevent="createOrder">
          <ValidationProvider
            class="form-group"
            rules="required|email"
            v-slot="{ errors, classes }"
          >
            <div class="form-group">
              <label for="useremail">Email</label>
              <input
                type="email"
                class="form-control"
                :class="classes"
                id="useremail"
                v-model="form.user.email"
                name="Email"
                placeholder=" Email"
              />
              <span class="text-danger">{{ errors[0] }}</span>
            </div>
          </ValidationProvider>

          <ValidationProvider
            class="form-group"
            rules="required"
            v-slot="{ errors, classes }"
          >
            <div class="form-group">
              <label for="username"></label>
              <input
                type="text"
                class="form-control"
                :class="classes"
                id="username"
                v-model="form.user.name"
                name=""
                placeholder=""
              />
              <span class="text-danger">{{ errors[0] }}</span>
            </div>
          </ValidationProvider>

          <ValidationProvider
            class="form-group"
            rules="required|numeric"
            v-slot="{ errors, classes }"
          >
            <div class="form-group">
              <label for="usertel"></label>
              <input
                type="tel"
                class="form-control"
                :class="classes"
                id="usertel"
                v-model="form.user.tel"
                name=""
                placeholder=""
              />
              <span class="text-danger">{{ errors[0] }}</span>
            </div>
          </ValidationProvider>

          <ValidationProvider
            class="form-group"
            rules="required"
            v-slot="{ errors, classes }"
          >
            <div class="form-group">
              <label for="useremail"></label>
              <input
                type="text"
                class="form-control"
                :class="classes"
                id="useraddress"
                v-model="form.user.address"
                name=""
                placeholder=""
              />
              <span class="text-danger">{{ errors[0] }}</span>
            </div>
          </ValidationProvider>

          <div class="form-group">
            <label for="comment"></label>
            <textarea
              name=""
              id="comment"
              class="form-control"
              cols="30"
              rows="10"
              v-model="form.message"
            ></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-danger" :disabled="invalid"></button>
          </div>
        </form>
      </ValidationObserver>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      products: [],
      product: [],
      cart: {},
      tempProduct: {},
      isNew: false,
      id: "",
      isLoading: false,
      status: {
        loadingItem: "",
      },
      coupon_code: "",
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
    };
  },
  methods: {
    getProducts(page = 1) {
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      // const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/products/all?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products;
        // console.log(vm.products);
        vm.isLoading = false;
      });
    },
    getProduct(id) {
      console.log(id);
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      // vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.product = response.data.product;
        console.log(response.data.product);
        $("#productModal").modal("show");
        vm.status.loadingItem = "";
        // vm.isLoading = false;
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(api, { data: cart }).then((response) => {
        console.log(response);
        vm.status.loadingItem = "";
        this.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.cart = response.data.data;
        // console.log(response);
        // console.log(vm.cart.carts.length)
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(api).then((response) => {
        console.log(response);
        this.getCart();
        vm.isLoading = false;
      });
    },
    addCoupon() {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = {
        code: vm.coupon_code,
      };
      vm.isLoading = true;
      this.$http.post(api, { data: coupon }).then((response) => {
        console.log(response.data);
        this.getCart();
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      vm.isLoading = true;
      this.$http.post(api, { data: order }).then((response) => {
        console.log("", response);
        if(response.data.success){
          vm.$router.push(`customer_checkout/${response.data.orderId}`)
        }
        // this.getCart();
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
  },
};
</script>




