<template>
  <div>
    <table class="table mt-4 table-hover">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col">Email</th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for='item in orders' :key="item.id">
          <th scope="row">{{ item.create_at }}</th>
          <td>{{ item.user.email }}</td>
          <td>
             <div v-for="product in item.products" :key="product.id">
            {{ product.product.title }}{{ product.product.num }}{{ product.product.unit }}
            </div>
          </td>
          <td>{{item.total| currency}}</td>
          <td>
            <span v-if="item.is_paid" class="text-success"></span>
            <span v-else></span>
          </td>
        </tr>
      </tbody>
    </table>

  </div>
</template>

<script>
export default {
  data() {
    return {
      orders:[],
      products:{},
    };
  },
  methods: {
    getOrders(page=1){
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
      const vm = this;
      this.$http.get(api).then((response) => {
        console.log(response.data);
        vm.orders = response.data.orders;
        vm.products=vm.orders.products;
        console.log(vm.products)
      });
    },
  },
  created() {
    this.getOrders();
  },
};
</script>




