<template>
  <div>
    <div class="text-right">
      <button class="btn btn-primary" @click='openModal'></button>
    </div>
    <table class="table mt-4 table-hover">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for='item in coupons' :key="item.id">
          <th scope="row">{{ item.title }}</th>
          <td>{{ item.percent }}</td>
          <td>{{ item.due_date}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-danger btn-sm" @click='delCoupon(item.id)'></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Modal -->
    <div class="modal" tabindex="-1" role="dialog" id='couponModal'>
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title"></h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="col-sm-12">
              <div class="form-group">
                <label for="title"></label>
                <input
                  type="text"
                  class="form-control"
                  id="title"
                  placeholder=""
                  v-model="coupon.title"
                />
              </div>
              <div class="form-group">
                  <label for="price">code</label>
                  <input
                    type="text"
                    class="form-control"
                    id="code"
                    placeholder=""
                    v-model="coupon.code"
                  />
                </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="percent"
                    placeholder=""
                    v-model="coupon.percent"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    type="timestamp"
                    class="form-control"
                    id="due_date"
                    placeholder=""
                    v-model="coupon.due_date"
                  />
                </div>
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="coupon.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>

              </div>
              <hr />
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click='updateCoupon'></button>
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      coupons:[],
      coupon:{},
    };
  },
  methods: {
    getCoupons(page=1){
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      const vm = this;
      this.$http.get(api).then((response) => {
        console.log(response.data);
        vm.coupons = response.data.coupons;
      });
    },
    openModal(){
      $("#couponModal").modal("show");
    },
    updateCoupon(){
      let api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let vm = this;
      this.$http.post(api, { data: vm.coupon }).then((response) => {
        console.log(response.data);
        if (response.data.success) {
          $("#couponModal").modal("hide");
          vm.getCoupons();
        } else {
          alert("");
        }
      });
    },
    delCoupon(id){
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${id}`;
      const vm = this;
      this.$http.delete(api).then((response) => {
        console.log(response.data);
        if (response.data.success) {
          vm.getCoupons();
        } else {
          alert("");
        }
      });
    }
  },
  created() {
    this.getCoupons();
  },
};
</script>




